body {
  background: #fff !important;
}

/* Main Heading Styles */
h1, h2, h3, h4, h5, h6, span, small, kbd, b, p, abbr, strong, i, em, cite, ins, mark, s, sub, sup, u, marquee, strike, samp, plaintext, label, dfn, a, windowText, strokeText, strokeText.letter-one-clr::first-letter, colorFullText, time {
  color: #333;
}

a:hover {
  filter: brightness(1.1) !important;
}

/* Selection Style */
::selection {
  background: rgba(17, 133, 211, 0.2666666667);
}

/* Button Outline Style */
button.outline {
  color: #333;
}
button.outline:hover {
  background: rgba(238, 238, 238, 0.2862745098);
}

/* Button Active Style */
button:hover.active {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ae6fff;
}
button:hover.active.primary {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #4493ee;
}
button:hover.active.secondary {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ff5100;
}
button:hover.active.success {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #00ffaa;
}
button:hover.active.danger {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ff514b;
}
button:hover.active.warning {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ffef62;
}
button:hover.active.info {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #62d5f1;
}
button:hover.active.light {
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #eee;
}

/* Button Light Style */
button.light {
  filter: brightness(0.9);
}

/* Drop Box & Drop Down Styles */
.drop-box .drop-heading {
  position: relative;
  --select-chervon_down2: url(/Icons/chervon_down_light.svg);
  background-image: var(--select-chervon_down2), none !important;
  background-repeat: no-repeat !important;
  background-position: right 0.75rem center !important;
  background-size: 12px !important;
  appearance: none;
  background: #eee;
  color: #333;
}
.drop-box .drop-heading:hover {
  color: #333;
  filter: brightness(0.9) !important;
}
.drop-box .drop-heading:hover:active {
  color: #333;
  filter: brightness(1) !important;
}

.drop-box Drop-down {
  background: #e7e6e6;
  color: #333;
}

/* Modal Style */
modal {
  background: #eee;
}
modal > modal-header {
  background: #f7f4f4;
}
modal > modal-header > modal-text {
  color: #333;
}
modal modal-btns {
  background: #eceaea;
  border-top: 1px solid rgba(2, 2, 2, 0.0549019608);
}
modal modal-content modal-text {
  color: #333;
}

/* Safe Area Styleing */
SafeArea[role=toast] {
  border: 1.5px solid rgba(2, 2, 2, 0.0549019608);
}
SafeArea[role=toast]:active {
  filter: brightness(0.9666555) !important;
}

/* Grouping Safe Area & More Classes */
SafeArea[role=toast],
SafeArea[role=notifications],
SafeArea[role=sidebar],
SafeArea[role=card],
nav,
.container,
.progress,
a.tooltiplink:hover::after,
.placeholder,
blockquote,
circle,
details,
window {
  background: #eee;
}

figure {
  background: #f7f4f4;
}

figcaption {
  color: #333;
}

a.tooltiplink:hover::after {
  color: #333;
  border: 1.5px solid rgba(2, 2, 2, 0.0549019608);
}

/* Placeholder Style */
@keyframes placeholder_fade {
  0%, 100% {
    filter: brightness(0.8);
  }
  50% {
    filter: brightness(0.9);
  }
}
window:before {
  color: #c2c1c1 !important;
}

blockquote:active {
  filter: brightness(1);
}

details:hover,
details:active {
  filter: brightness(1);
}

